import React, { useEffect, useState } from 'react';
import { FC } from 'react';
import {
  AnswerContent,
  AnswerDescription,
  AnswerItem,
  Answers,
  AnswerTitle,
  CheckBox,
  FeatureIconContainer,
  FeatureText,
  Label,
  PreviewImage,
  Question,
  TestResult,
  StyledFeatureBlock,
  StyledKnowledgeTest,
  StyledVideoPreview,
  VideoTitle,
  StlyedAnswerResult,
} from './styled';
import { Text } from '../common/Text';
import YoutubePlayIcon from '../common/Icons/YoutubePlayIcon';
import { colors } from '../../utils/colors';
import { StaticImage } from 'gatsby-plugin-image';
import coldEmoji from '../../images/Emoji/cold.png';
import warmEmoji from '../../images/Emoji/warm.png';
import hotEmoji from '../../images/Emoji/hot.png';
import { InternalLinks } from '../../utils/links';
import { Link } from 'gatsby';
import { Modal } from '../common/Modal';
import { VideoContainer } from '../common/styled';

type Props = {
  image: React.ReactElement;
  title: string;
  backgroundColor: string;
  size: 'S' | 'M';
  children: React.ReactNode;
};

export const VideoPreview: FC<Props> = ({
  size,
  backgroundColor,
  title,
  image,
  children,
}) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <StyledVideoPreview
        size={size}
        backgroundColor={backgroundColor}
        onClick={() => setIsOpen(true)}
      >
        <PreviewImage>
          <YoutubePlayIcon />
          {image}
        </PreviewImage>
        <VideoTitle>
          <Text.Regular.Bold.md>{title}</Text.Regular.Bold.md>
        </VideoTitle>
      </StyledVideoPreview>
      {isOpen && (
        <Modal onClose={() => setIsOpen(false)} showCloseButton>
          <VideoContainer>{children}</VideoContainer>
        </Modal>
      )}
    </>
  );
};

type BenefitBlockProps = {
  icon: React.ReactElement;
  title: string;
  text: React.ReactElement;
};

export const FeatureBlock: FC<BenefitBlockProps> = ({ icon, text, title }) => {
  return (
    <StyledFeatureBlock>
      <FeatureIconContainer>{icon}</FeatureIconContainer>
      <div>
        <p>
          <Text.Regular.Bold.md>{title}</Text.Regular.Bold.md>
        </p>
        <FeatureText>
          <Text.Regular.Normal.md color={colors.primary.gray500}>
            {text}
          </Text.Regular.Normal.md>
        </FeatureText>
      </div>
    </StyledFeatureBlock>
  );
};

enum AnswerType {
  Cold = 'cold',
  Warm = 'warm',
  Hot = 'hot',
}

type AnswerItem = {
  title: string;
  description: React.ReactElement;
  type: AnswerType;
};

const AnswerResult: FC<{
  emoji: React.ReactElement;
  text: string;
  textColor: string;
}> = ({ emoji, text, textColor }) => {
  return (
    <StlyedAnswerResult color={textColor}>
      <Text.Regular.Normal.xs>{text}</Text.Regular.Normal.xs>
      {emoji}
    </StlyedAnswerResult>
  );
};

const mapAnswerTypeToDisplayText = {
  [AnswerType.Cold]: (
    <AnswerResult
      text="cold"
      textColor={colors.primary.blue}
      emoji={<img src={coldEmoji} alt="cold" />}
    ></AnswerResult>
  ),
  [AnswerType.Warm]: (
    <AnswerResult
      text="warm"
      textColor={colors.primary.lightOrange}
      emoji={<img src={warmEmoji} alt="warm" />}
    ></AnswerResult>
  ),
  [AnswerType.Hot]: (
    <AnswerResult
      text="hot!"
      textColor={colors.secondary.lightRed}
      emoji={<img src={hotEmoji} alt="hot" />}
    ></AnswerResult>
  ),
};

export const KnowledgeTest = () => {
  const [checked, setChecked] = useState<AnswerType[]>([]);
  const [result, setResult] = useState('Click on one answer');

  const answers: AnswerItem[] = [
    {
      title: 'Compliance',
      description: (
        <>Your A2P 10DLC traffic must be compliant in order to register with TCR. </>
      ),
      type: AnswerType.Cold,
    },
    {
      title: 'Improved deliverability',
      description: (
        <>
          Running registered traffic results in less filtering and therefore better
          deliverability, but it’s technically not what you’re paying for. 
        </>
      ),
      type: AnswerType.Warm,
    },
    {
      title: 'Higher throughput',
      description: (
        <>
          What you’re fundamentally paying for with TCR registration is{' '}
          <Link
            to={`${InternalLinks.LatestNews}food-for-thought-what-are-you-paying-for-when-you-register-with-the-campaign-registry/`}
          >
            higher throughput{' '}
          </Link>
          or rate limits based on your messaging use case. Registering is the only way to
          achieve these limits.
        </>
      ),
      type: AnswerType.Hot,
    },
  ];

  useEffect(() => {
    if (checked.includes(AnswerType.Hot)) {
      setResult('You got it!');
      return;
    }
    if (checked.includes(AnswerType.Warm)) {
      setResult('Getting warmer!');
      return;
    }
    if (checked.includes(AnswerType.Cold)) {
      setResult('Close, try again!');
      return;
    }
  }, [checked]);

  return (
    <StyledKnowledgeTest>
      <Label>
        <Text.Regular.Normal.xs>Test your knowledge</Text.Regular.Normal.xs>
      </Label>
      <Question>
        <Text.Regular.Bold.md>
          What are you paying for by registering with TCR?
        </Text.Regular.Bold.md>
      </Question>
      <TestResult>
        <Text.Regular.Normal.md>{result}</Text.Regular.Normal.md>
      </TestResult>
      <Answers>
        {answers.map((answer) => {
          const isAnswerChecked = checked.includes(answer.type);
          return (
            <AnswerItem
              onClick={() => setChecked((st) => [...st, answer.type])}
              checked={isAnswerChecked}
              key={answer.type}
            >
              <CheckBox checked={isAnswerChecked}></CheckBox>
              <AnswerContent>
                <AnswerTitle>
                  <Text.Regular.Normal.md>{answer.title}</Text.Regular.Normal.md>
                  {isAnswerChecked && mapAnswerTypeToDisplayText[answer.type]}
                </AnswerTitle>
                {isAnswerChecked && (
                  <AnswerDescription>
                    <Text.Regular.Normal.xs>{answer.description}</Text.Regular.Normal.xs>
                  </AnswerDescription>
                )}
              </AnswerContent>
            </AnswerItem>
          );
        })}
      </Answers>
    </StyledKnowledgeTest>
  );
};
