type TAlgoliaNode = {
  id: string;
  frontmatter: {
    title: string;
    metaDescription: string;
  };
  fields: {
    slug: string;
  };
  excerpt: string;
};

enum AlgoliaIndexes {
  News = 'news',
  terms = 'terms',
  Policies = 'policies',
  Bios = 'bios',
}

const getQuery = (indexName: string) => `{
  pages: allMarkdownRemark(
    filter: {
      fileAbsolutePath: { regex: "//${indexName}//" },
    }
  ) {
    edges {
      node {
        id
        frontmatter {
          title
          metaDescription
        }
        fields {
          slug
        }
        excerpt(pruneLength: 300)
      }
    }
  }
}`;

function pageToAlgoliaRecord({
  node: { id, frontmatter, fields, ...rest },
}: {
  node: TAlgoliaNode;
}) {
  return {
    objectID: id,
    ...frontmatter,
    ...fields,
    ...rest,
  };
}

const queries = Object.values(AlgoliaIndexes).map(indexName => ({
        query: getQuery(indexName),
        transformer: ({ data }: any) => data.pages.edges.map(pageToAlgoliaRecord),
        indexName,
}))

module.exports = queries;